import {
  useFeedbackStudentFeedbackListQuery,
  useFeedbackSubjectTeacherListQuery,
  useFeedbackCreateStudentFeedbackMutation,
  useFeedbackDetailStudentFeedbackQuery,
  useFeedbackUpdateStudentFeedbackMutation,
  useFeedbackRemoveStudentFeedbackMutation,
  useFeedbackGiveStudentFeedbackMutation,
  useFeedbackQuestionCreateMutation,
  useFeedbackQuestionUpdateMutation,
  useFeedbackQuestionRemoveMutation,
  useFeedbackTakenMutation,
  useFeedbackAnalyticProcessMutation,
  useFeedbackGivenStudentListQuery,
  useFeedbackOneStaffAnalyticQuery,
  useFeedbackUpdateCloseDateMutation,
  useFeedbackOneStaffInfoQuery,
  useFeedbackDepartmentAnalyticExcelExportMutation,
} from "../../../Services/InstituteFeedback/instituteFeedbackApiDynamic";

export const useFeedbackStudentFeedbackList = (args) => {
  const { data, refetch, isFetching } = useFeedbackStudentFeedbackListQuery(
    args.data,
    {
      skip: args.skip,
    }
  );
  return {
    feedbackStudentFeedbackList: data,
    feedbackStudentFeedbackListRefetch: refetch,
    feedbackStudentFeedbackListLoading: isFetching,
  };
};

